.profile-details-inner{background: #fff;
    padding: 20px 0px 0px;
    border-radius: 18px;
    box-shadow: 0 5px 10px 0 rgb(204 204 204 / 16%);
    margin-bottom: 26px;
    overflow: hidden;
}

.profile-C-M{display: flex; align-items: center; padding: 6px 26px;}

.profile-1-B-inner {
    background: #70cada;
    padding: 6px 15px;
    border-radius: 8px;
    color: #fff;
    font-size: 13px;
    margin-left: 30px;
}

.btn.edit-details-T{
	border-radius: 20px;
    padding: 8px 18px;
    background: #348391;
    color: #fff;
    box-shadow: none;
    font-size: 12px;
    margin-top: 32px;
}

.btn.clinical-btn{
	background: #f0f8f9;
    padding: 8px 24px;
    border-radius: 30px;
    color: #266e7b;
    box-shadow: none;
    text-transform: inherit;
    position: absolute;
    top: -25px;
    border: 1px solid #266e7b;
    left: 24px;
}

.clinical-test{
	background: #fbfbfb;
    padding: 16px 20px;
    border-radius: 0px;
    position: relative;
    border-bottom: 1px solid #f5efef;
    margin-top: 50px;
    border-top: 1px solid #f5efef;
}

.modal-footer{
	background: #eaf8fb;
	padding: .6rem !important;
	border-top: 1px solid #d0e6ea;
}

.btn-group-popup {
    margin: auto;
}


.Assignment-detail p{margin-bottom: 6px;}

.Assignment-detail {
    padding-right: 22px;
    margin-top: 32px;
}

.Heading-assign{
	background: #eaebec; padding: 8px 10px; margin-top: 10px; margin-top: 40px;
}

.btn:hover {
    color: #266e7b !important;
}

.btn.addBtn.btn-next:hover{
	color: #fff !important;
}


.btn-back {
    background: transparent;
    color: #266e7b;
    padding: 7px 30px;
    border-radius: 10px;
    margin-left: 0px;
    margin: 20px 10px 18px;
    border: 1px solid #266e7b;
    box-shadow: none;
    text-transform: inherit;
}

.profile-pic {
    height: 74px;
    width: 74px;
    border-radius: 60px;
    background: #ccc;
    display: inline-block;
}

.details-dr p {
    margin-bottom: 0px;
}

.doctors-H-C {
    color: #b9b7b7;
    margin-top: 30px;
}

.border-R-1  p{margin-bottom: 0px; border-right: 1px solid #e4e4e4;}
.border-R-2  p{margin-bottom: 0px;}